<!--
 * @Description: 角色详情信息弹出框模块
 * @LastEditors: Ji Yinghao
 * @Date: 2019-05-05 13:42:39
 * @LastEditTime: 2019-09-25 10:23:04
 -->
<template>
  <div id="switch__">
    <!-- <v-btn color="primary" @click="dialog = true" >+ 创建子账号123 </v-btn> -->
    <v-layout row justify-center>
      <v-dialog v-model="dialog" max-width="800px">
        <!-- <template v-slot:activator="{ on }">
          <v-btn color="primary" flat dark v-on="on">+ 创建 {{ dialog }} </v-btn>
        </template>-->

        <v-card>
          <v-card-title>
            <span class="headline">订单详情</span>
          </v-card-title>
          <v-card-text>
            <!-- <p class="root_p">
              <v-img
                :src="data.head_url"
                lazy-src="https://picsum.photos/id/11/10/6"
                aspect-ratio="1"
                class="grey lighten-2"
                max-width="60"
                @click="img_(data.head_url)"
              ></v-img>
            </p> -->

             <p class="root_p">
               <span class="order_title">用户账号</span>
              <v-chip>{{ data.user_phone | cover }} / {{ data.user_name }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title"> 用户订单号 </span>
              <v-chip>{{ data.order_no }}</v-chip>
            </p>

            <p class="root_p">
               <span class="order_title">商户订单号</span>
              <v-chip>{{ data.out_trade_no }}</v-chip>
            </p>

            <p class="root_p">
               <span class="order_title">订单金额</span>
              <v-chip>{{ data.amt?(data.amt).toFixed(2):"0.00" }}元</v-chip>
            </p>

           

            <p class="root_p">
               <span class="order_title">医生账号</span>
              <v-chip>{{ data.doctor_phone | cover }} / {{ data.doctor_name }}</v-chip>
            </p>

            <p class="root_p">
               <span class="order_title">订单创建时间</span>
              <v-chip>{{ data.create_time | time }}</v-chip>
            </p>

            <p class="root_p">
               <span class="order_title">订单更新时间</span>
              <v-chip>{{ data.update_time | time }}</v-chip>
            </p>

            <p class="root_p">
               <span class="order_title">订单过期时间</span>
              <v-chip>
                <span v-if="data.expire_date == 0"> 未激活 </span>
                <span v-else> {{ data.expire_date | time }} </span>
              </v-chip>
            </p>

            <p class="root_p">
               <span class="order_title">已发消息次数</span>
              <v-chip v-if="data.current_index == 0 && data.expire_date == 0"> 未使用 </v-chip>
              <v-chip v-else>{{ data.current_index }}</v-chip>
            </p>

            <p class="root_p">
                <span class="order_title">订单状态</span>
                <v-chip color="" v-if="data.status == 3000">订单未支付</v-chip>
                <v-chip color=""  v-if="data.status == 3001">订单已关闭</v-chip>
                <v-chip color="green" text-color="white" v-if="data.status == 3002">用户支付中</v-chip>
                <v-chip color="red"  text-color="white" v-if="data.status == 3003">订单已撤销</v-chip>
                <v-chip color="red" v-if="data.status == 3004">转入退款</v-chip>
                <v-chip color="red"  text-color="white" v-if="data.status == 3005">支付失败</v-chip>
                <v-chip color="green"  text-color="white" v-if="data.status == 3006">支付成功</v-chip>
                <v-chip color="green" text-color="white" v-if="data.status == 3010">订单使用中</v-chip>
                <v-chip color="red" text-color="white" v-if="data.status == 3011">订单已失效</v-chip>
                <v-chip color="green" text-color="white" v-if="data.status == 3012">订单已评价</v-chip>
            </p>

            <!-- <p class="root_p" >已获得的权限</p>
                  <v-flex md12 sm12>
                   <v-chip class="text-xs-center" v-for="(item,index) in data.root_path" :key="index" >{{ item.name }}</v-chip> 
            </v-flex>-->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <!-- <v-btn color="blue darken-1" flat @click="dialog = false">取消</v-btn> -->
            <v-btn color="info darken-1" @click="dialog = false">确认</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>

    <!-- 右上角提示框 -->
    <v-snackbar
      v-model="alert.snackbar"
      :right="true"
      :timeout="alert.timeout"
      :top="true"
      :multi-line="true"
    >
      {{ alert.text }}
      <v-btn color="pink" flat @click="alert.snackbar = false">确认</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      alert: {
        text: "",
        timeout: 3000,
        snackbar: false
      },
      dialog: false,
      data: {}
    };
  },
  // 更新完成
  mounted: function() {},
  methods: {
    /** 初始化数据 */
    info(data) {
      this.dialog = data.state;
      this.data = data.list;
    },
    /** 图片预览 */
    img_( img ){
      window.console.log( document.body.clientHeight )
      window.open(img,'newwindow', `height=600, width=800, top=${ document.body.clientHeight/2 - 200 }, left=${ document.body.clientWidth/2 - 400 }, toolbar=no, menubar=no, scrollbars=no, resizable=no,location=no, status=no`);
    }
  }
};
</script>

<style lang="less">
#switch__ {
  .v-input__slot {
    margin-bottom: 0px !important;
  }
  .v-input--selection-controls {
    margin-top: 0px !important;
  }
  .v-list__tile__content {
    display: block !important;
  }
  .v-list__tile--avatar {
    min-height: 70px;
    display: inline-table;
  }
}
.root_p {
  font-size: 16px;
  padding-left: 17px;
}
.order_title{
  display: inline-block;
  width: 125px;
}
</style>
