<template>
  <div>
    <!-- 导航 -->
    <Breadcrumbs :items="items" />
    <!-- <Module1 ref="module1" /> -->
    <Module2 ref="module2" />
    <!-- 数据表格 -->
    <v-card>
      <v-card-title>
        <v-layout row wrap>
          <v-flex xs6 sm4 md2>
            <v-text-field v-model="order_no" label="用户订单号"></v-text-field>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <v-text-field v-model="out_trade_no" label="用户账号"></v-text-field>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <v-select
              @change="info"
              menu-props="auto"
              v-model="status"
              :items="agent_list"
              item-text="text"
              item-value="value"
              return-object
              label="订单状态"
              required
            ></v-select>
          </v-flex>
          <v-btn :loading="loader" :disabled="loader" color="primary" @click="info">筛选用户</v-btn>
        </v-layout>
      </v-card-title>
      <!--
        headers : 表头
        items： 表格内容
        total-items ： 总共的数据数量 （手动设置项目的总数，就可以禁用内置排序和分页）
        disable-initial-sort 在初始渲染时禁用默认排序
        :hide-actions="true" 隐藏表格操作
        :dark="true" 暗主题
        :loading="true" 加载进度条
      -->
      <v-data-table
        :headers="headers"
        :items="desserts"
        class="elevation-1"
        :total-items="allList"
        :rows-per-page-text="`每页行数`"
        :rows-per-page-items="pagingList"
        :disable-initial-sort="true"
        :loading="loader"
        @update:pagination="info"
      >
        <template v-slot:items="props">
          <td class="text-xs-centre">
            {{ `===无===` }}
            <!-- <v-chip small v-for="(item,index) in props.item.agent_name" :key="index">{{ item }}</v-chip> -->
          </td>
          <td class="text-xs-centre">
            <v-tooltip top>
              <template v-slot:activator="{ on }">
                <span color="primary" dark v-on="on">{{ props.item.order_no | slice_10 }}</span>
              </template>
              <span>{{ props.item.order_no }}</span>
            </v-tooltip>
          </td>
          <td class="text-xs-centre">
            <v-tooltip top>
              <template v-slot:activator="{ on }">
                <span color="primary" dark v-on="on">{{ props.item.out_trade_no | slice_10 }}</span>
              </template>
              <span>{{ props.item.out_trade_no }}</span>
            </v-tooltip>
          </td>
          <td class="text-xs-centre">{{ (props.item.amt).toFixed(2) }} 元</td>
          
          <td class="text-xs-centre">
            {{ `===无===` }}
            <!-- <v-chip small v-for="(item,index) in props.item.agent_name" :key="index">{{ item }}</v-chip> -->
          </td>
          <td class="text-xs-centre">{{ props.item.create_time | time }}</td>
          <td class="text-xs-centre">{{ props.item.update_time | time }}</td>
          <td class="text-xs-centre">
            <span v-if="props.item.expire_date == 0">未激活</span>
            <span v-else>{{ props.item.expire_date | time }}</span>
          </td>
          <td class="text-xs-centre">
            <span v-if="props.item.current_index == 0 && props.item.expire_date == 0">未使用</span>
            <span v-else>{{ props.item.current_index }}</span>
          </td>
          <td class="text-xs-centre">
            <!--
'订单状态3000未支付,3010已支付,3012使用中,3013订单已过期或失效,3015已关闭订单,3016退款中,3017退款失败,3018已退款,3019未知错误',
            <v-chip color="green" text-color="white" v-if="props.item.status == 3030">已转入医生账户</v-chip>
            <v-chip color="" text-color="white" v-if="props.item.status == 3031">提现申请中</v-chip>
            <v-chip color="primary" text-color="white" v-if="props.item.status == 3032">提现处理中</v-chip>
            <v-chip color="green" text-color="white" v-if="props.item.status == 3033">提现到账</v-chip>
            <v-chip color="red" text-color="white" v-if="props.item.status == 3034">提现失败</v-chip>
            <v-chip color="red" text-color="white" v-if="props.item.status == 3035">提现失败-金额退回医生账号</v-chip>
            -->
            <v-chip color="" v-if="props.item.status == 3000">未支付</v-chip>
            <v-chip color="green" text-color="white" v-if="props.item.status == 3010">已支付</v-chip>
            <v-chip color="green" text-color="white" v-if="props.item.status == 3012">使用中</v-chip>
            <v-chip color="red"  text-color="white" v-if="props.item.status == 3013">已过期</v-chip>
            <v-chip color="" v-if="props.item.status == 3015">已关闭</v-chip>
            <v-chip color="red"  text-color="white" v-if="props.item.status == 3016">退款中</v-chip>
            <v-chip color="red"  text-color="white" v-if="props.item.status == 3017">退款失败</v-chip>
            <v-chip color="red"  text-color="white" v-if="props.item.status == 3018">已退款</v-chip>
            <v-chip color="red"  text-color="white" v-if="props.item.status == 3019">未知状态</v-chip>
            <v-chip color="green" text-color="white" v-if="props.item.status == 3014">已完成</v-chip>
          </td>
          <td class="text-xs-centre">
            <v-menu
              bottom
              dark
              origin="center center"
              :close-delay="100"
              transition="slide-x-transition"
            >
              <template v-slot:activator="{ on }">
                <v-btn color="primary" dark v-on="on">订单操作</v-btn>
              </template>
              <v-list>
                <v-list-tile  @click="info_data( props.item )">
                  <v-list-tile-title>订单详情</v-list-tile-title>
                </v-list-tile>
                <v-list-tile  @click="manages_list( props.item.msg_index )">
                  <v-list-tile-title>聊天记录</v-list-tile-title>
                </v-list-tile>
              </v-list>
            </v-menu>
          </td>
        </template>
        <!-- 当没有数据的时候，可以使用 no-data 插槽显示一段自定义HTML。 -->
        <template v-slot:no-data>
          <p class="p_text">退款订单已处理完了 :(</p>
        </template>
        <!-- 如果你还想添加一些额外的功能，比如每列过滤或者搜索，你可以使用 footer 插槽。 -->
        <!-- <template v-slot:footer>
          <td :colspan="headers.length">
            <strong>总计：</strong>
          </td>
        </template>-->
        <!-- 你可以使用page-text 插槽来自定义显示范围以及总条目。 -->
        <template
          v-slot:pageText="props"
        >当前 {{ props.pageStart }} - {{ props.pageStop }} 总共 {{ props.itemsLength }}</template>
      </v-data-table>
    </v-card>
    <!-- 确认框 -->
    <v-dialog v-model="dialog_logout" max-width="290">
      <v-card>
        <v-card-title class="headline">删除操作</v-card-title>
        <v-card-text>确认删除该用户吗？确认删除将不可恢复</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat="flat" @click="dialog_logout = false">取消</v-btn>
          <v-btn color="green darken-1" flat="flat" @click="delMessage">确认删除</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";
// import Module1 from "../orderList/components/module1.vue";
import Module2 from "../orderList/components/module2.vue";
export default {
  components: {
    Breadcrumbs,
    // Module1,
    Module2
  },
  data() {
    return {
      // 确认框
      dialog_logout: false,
      // 订单状态
      agent_list: [
        // {
        //   text: "全部",
        //   value: 0
        // },
        // {
        //   text: "未支付",
        //   value: 3000
        // },
        // {
        //   text: "已支付",
        //   value: 3010
        // },
        // {
        //   text: "使用中",
        //   value: 3012
        // },
        // {
        //   text: "已过期",
        //   value: 3013
        // },
        // {
        //   text: "已完成",
        //   value: 3014
        // },
        // {
        //   text: "已关闭",
        //   value: 3015
        // },
        {
          text: "退款中",
          value: 3016
        },
        {
          text: "退款失败",
          value: 3017
        },
        {
          text: "已退款",
          value: 3018
        },
        {
          text: "未知状态",
          value: 3019
        }
      ],
      // 筛选数据
      user_id: 0,
      doctor_id: 0,
      status: { text: "退款中", value: 3016},
      page: 0,
      page_count: 10,
      out_trade_no: "",
      order_no: "",
      // 分页数据
      pagingList: [10, 20, 30, 40, 50, { text: "100", value: 100 }],
      loader: false,
      allList: 0,
      // 导航数据
      items: [
        {
          text: "首页",
          disabled: false,
          to: "/home"
        },
        {
          text: "退款订单列表",
          disabled: false,
          to: "/refundOrder"
        }
      ],
      // 表格数据
      headers: [
        { text: "用户账号", value: "fat", sortable: false },
        { text: "用户订单号", align: "left", sortable: false, value: "name" },
        { text: "商户订单号", value: "carbs", sortable: false },
        { text: "订单金额", value: "calories", sortable: false },
        { text: "医生账号", value: "fat", sortable: false },
        { text: "订单创建时间", value: "protein", sortable: false },
        { text: "订单更新时间", value: "iron", sortable: false },
        { text: "订单过期时间", value: "iron", sortable: false },
        { text: "已发消息次数", value: "iron", sortable: false },
        { text: "订单状态", value: "iron", sortable: false },
        { text: "操作", value: "iron", sortable: false }
      ],
      desserts: [],
      // 权限数据
      root_path: this.$cookie.getLocalStorage("root_path").root_path
    };
  },
  mounted: function() {
    // this.info();
  },
  methods: {
    // 订单详情
    info_data(list) {
      let datas = {
        state: true,
        list: list
      };
      this.$refs.module2.info(datas);
    },
    // 删除订单
    delMessage() {},
    /* 聊天记录 */
    manages_list( manages ){
      this.$router.push({
                path: `/chatList`,
                query:{
                  data: manages
                }
              });


      // this.$axios({
      //   method: "post",
      //   url: this.$apis.URL.manages_messages,
      //   data: {
      //     msg_index: manages
      //   }
      // })
      //   .then(response => {
      //     if (response.data.result === 0) {
      //       window.console.log(response.data);
      //       this.$router.push({
      //           path: `/chatList`,
      //           query:{
      //             data: response.data
      //           }
      //         });
      //     } else {
      //       this.alert.snackbar = true;
      //       this.alert.text = response.data.message;
      //     }
      //     this.loader = false;
      //   })
      //   .catch(error => {
      //     this.alert.snackbar = true;
      //     this.alert.text = error;
      //   });
    },
    // 用户列表
    info(event) {
      window.console.log(event);
      window.console.log(typeof event == "undefined");
      this.loader = true;
      this.$axios({
        method: "post",
        url: this.$apis.URL.order_list,
        data: {
          user_id: this.user_id,
          doctor_id: this.doctor_id,
          status: this.status.value,
          page: typeof event == "undefined" ? this.page : event.page - 1,
          page_count:
            typeof event == "undefined" ? this.page_count : event.rowsPerPage,
          out_trade_no: this.out_trade_no,
          order_no: this.order_no
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.desserts = response.data.datas.list;
            this.allList = response.data.datas.total_number;
            window.console.log(this.desserts);
          } else {
            this.$Dialog.getProductCheckResult_({
                text: response.data.message,
                timeout: 3000,
                snackbar: true
            })
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({
                text: error,
                timeout: 3000,
                snackbar: true
            })
        });
    }
  },
  watch: {}
};
</script>

<style lang="less" scoped>
.flex {
  padding-right: 10px;
  padding-left: 10px;
}
</style>


